@import "src/assets/styles/variables";

.search-page {
  @extend %center-column;
  justify-content: space-between;
  height: 100%;

  &__header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    display: flex;
    align-items: center;
    background-color: $white;
    height: 80px;
  }

  &__header-logo {
    margin-left: 124px;
  }

  &__content {
    @extend %center-column;
    margin-top: 224px;
  }

  &__title {
    font-size: 2.25rem;
    color: $green;
    margin-bottom: 2.688rem;
  }

  &__input-wrapper {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    gap: 1.063rem;
  }

  &__zip-code {
    z-index: 1;
  }
}
