@import "src/assets/styles/variables";

.autocomplete {
  &__item {
    background-color: $white;
  }

  &__item--selected {
    background-color: $pink;
    color: $white;
  }

  &__item,
  &__item--selected {
    display: flex;
    align-items: center;
    padding-left: 3.875rem;
    height: 2.5rem;
  }
}
