@import "src/assets/styles/variables";

.dropdown {
  position: relative;
  z-index: 3;
  &__header {
    @extend %center-row;
    justify-content: space-between;
    min-width: 209px;
    height: 2.438rem;
    background-color: $white;
    cursor: pointer;

    border-radius: 0.625rem;
    border: 0.125rem solid $green;
    padding-left: 1rem;
    padding-right: 0.875rem;

    font-size: 1.125rem;
    font-family: "Roboto", sans-serif;
    color: $results-text;
  }

  &__items {
    display: flex;
    flex-direction: column;
    position: absolute;
    width: 100%;
  }

  &__item {
    position: relative;
    background-color: $white;
    cursor: pointer;
    padding-left: 1rem;
    color: $results-text;
    font-size: 1.125rem;
    font-family: "Roboto", sans-serif;
    width: 100%;
    padding-top: 0.469rem;
    padding-bottom: 0.469rem;
  }
}
